import { 
  Hooper, 
  Slide,
  Navigation  as HooperNavigation  //슬라이드 화살표
} from 'hooper';
import 'hooper/dist/hooper.css';

export default {
  name: 'profileEdit',
  // layout: 'default',
  components: {
    Hooper, 
    Slide,
    HooperNavigation 
  },
  data: () => ({
    profilePhotoOn    : false,
    select            : false,
    count             : 1,
    selectIdx         : 0,
    badgeList         :[
      {
        img       :'~assets/img/test/test-card8.png',
        name      :'새싹',
        idx       : 0,
      },
      {
        img       :'~assets/img/test/test-card8.png',
        name      :'꽃',
        idx       : 1,
      },
      {
        img       :'~assets/img/test/test-card8.png',
        name      :'저울',
        idx       : 2,
      },
      {
        img       :'~assets/img/test/test-card8.png',
        name      :'저울',
        idx       : 3,
      },
      {
        img       :'~assets/img/test/test-card8.png',
        name      :'저울',
        idx       : 4,
      },
      {
        img       :'~assets/img/test/test-card8.png',
        name      :'저울',
        idx       : 5,
      },
      {
        img       :'~assets/img/test/test-card8.png',
        name      :'저울',
        idx       : 6,
      },
      {
        img       :'~assets/img/test/test-card8.png',
        name      :'저울',
        idx       : 7,
      }
    ],
    hooperSetting: {
      itemsToShow : 1, 
    },
  }),
  methods: {
    selectBadge(idx, event){
      
      let activeClass = event.target.classList.contains('active')
      if(!activeClass){
        this.count++
        if(this.count < 4){
        event.target.classList.add('active')
      }
        } else if(activeClass){
        this.count--
        if(this.count < 4){
        event.target.classList.remove('active')
      }
        }
      

      
    },
    
  }
}
